<template>
    <div id="app">
        <DP_Popup
                :display.sync="globalTips.show"
                :show-close="globalTips.showClose"
                :icon="globalTips.icon"
                background-color="white"
                :title="globalTips.title"
        >
            {{globalTips.content}}
        </DP_Popup>
        <transition name="fade">
            <div v-show="!requestCompleted" class="pd-loading"></div>
        </transition>
        <!--        <router-view v-show="requestCompleted"/>-->
        <router-view/>
    </div>
</template>
<script>
    // import DP_Dev from '@/components/DP_Dev';;
    import {mapGetters, mapMutations} from "vuex";
    // import {getLocalData} from "./utils/function";
    // import {loginByUnionId} from "@/api/login";
    import DP_Popup from '@/components/DP_Popup'
    import {getEnv} from "@/utils/function";

    export default {
        name: "App",
        components: {
            DP_Popup
        },
        props: {},
        data() {
            return {
                wow: null,
            }
        },
        watch: {
            browser: {
                handler(val) {
                    //这里判断设备 是为了预渲染插件 不渲染  弹窗提示
                    if (val.device.isIos || val.device.isAndroid || val.device.isWechat) {
                        if (val.isHorizontallyScreen) {

                            this.dp_Tips('提示', '请竖屏查看', '', false);
                        } else {
                            if (this.$store.getters.getGlobalTips.content === '请竖屏查看') {
                                this.dp_CloseTips();
                            }
                        }
                    }

                },
                deep: true,
            },

        },
        computed: {
            ...mapGetters({
                pageParameter: "getPageParameter",
                browser: "getBrowser",
                requestCompleted: "getRequestCompleted",
                showRegister: "getShowRegister",
                globalTips: "getGlobalTips",
            }),
        },
        methods: {
            ...mapMutations(['setEnv', 'setBrowser', 'setDevice', 'setToken', 'setUserInfo']),
            checkEnv() {
                const env = getEnv();
                console.log('检查环境', env);
                this.setEnv(env);
            },
            checkBrowser() {
                const ua = window.navigator.userAgent.toLocaleLowerCase();
                if (/iphone/.test(ua)) this.setDevice({isIos: true, isIphone: true,});
                if (/ipad/.test(ua)) this.setDevice({isIos: true, isIpad: true,});
                if (/android/.test(ua)) this.setDevice({isAndroid: true});
                if (/micromessenger/.test(ua)) this.setDevice({isWechat: true});
                if (/drpanda_app=/.test(window.location.href)) this.setDevice({isDPApp: true});// 使用熊猫博士app打开web  带上参数drpanda_app=app名称

            },
            checkUrl() {
                if (window.location.href.indexOf('?redirect-path=') !== -1) {
                    let path = '/' + window.location.href.split('?redirect-path=')[1];// 不使用dp_getQueryString 是因为要把重定向path 后面的 参数一并带过去
                    this.$router.push(path);
                }
                // const path = this.dp_getQueryString('redirect-path');
                // if (path){
                //     console.log(path);
                //     this.$router.push("/"+path);
                // }

            },
            checkHeight() {
                const height = document.documentElement.clientHeight || document.body.clientHeight;
                this.setBrowser({clientHeight: height + 'px'})
            },
            checkHorizontallyScreen() {

                /**
                 * 存在输入法 弹出 竖屏被检测为横屏的问题
                 */
                // const width = document.documentElement.clientWidth
                // const height = document.documentElement.clientHeight
                // if (width > height) {
                //     this.setBrowser({isHorizontallyScreen: true});
                // }else {
                //     this.setBrowser({isHorizontallyScreen: false});
                // }

                console.log(screen.orientation)
                //0,90，-90  分别为竖屏  右侧横屏 左侧横屏
                this.setBrowser({isHorizontallyScreen: screen.orientation.angle !== 0});


            },
        },
        created() {
            this.checkEnv();
            this.checkBrowser();
            this.checkUrl();

        },
        mounted() {
            this.checkHeight();
            this.checkHorizontallyScreen();
            window.addEventListener('resize', () => {
                this.checkHeight();
            })
            window.addEventListener('orientationchange', () => {
                this.checkHorizontallyScreen();
            }, false)
        },
    }
</script>
<style lang="scss">
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
    }

    @keyframes shadow {
        from {
            box-shadow: 0 0 20px 5px #afafaf;

        }
        to {
            box-shadow: 0 0 120px 1px #afafaf;
            transform: scale(1.1);
        }
    }

    #app {

    }

    /*<!--.pd-loading {-->*/
    /*<!--    !*background-color: transparent;*!-->*/
    /*<!--    !*height: 100vh;*!-->*/
    /*<!--    width: 120px;-->*/
    /*<!--    height: 120px;-->*/
    /*<!--    background-image: url("./assets/global/material/pda.png");-->*/
    /*<!--    background-size: 120px 120px;-->*/
    /*<!--    background-size: cover;-->*/
    /*<!--    background-repeat: no-repeat;-->*/
    /*<!--    background-position: center center;-->*/
    /*<!--    position: absolute;-->*/
    /*<!--    top: 50%;-->*/
    /*<!--    left: 50%;-->*/
    /*<!--    margin-left: -60px;-->*/
    /*<!--    margin-top: -200px;-->*/
    /*<!--    border-radius: 50%;-->*/
    /*<!--    animation: shadow 1s linear infinite alternate;-->*/
    /*<!--    opacity: .8;-->*/
    /*<!--}-->*/

    #nav {
        position: fixed;
        top: 0;
        left: 0;
        background-color: #2c3e50;
        width: 100%;
        z-index: 99999999999;
        opacity: .7;
        font-size: 34px;
    }

    #nav a {
        font-weight: bold;
        color: white;
        margin-left: 15px;
    }

    #nav a.router-link-exact-active {
        color: #42b983;
    }

    .loading {
        width: 300px;
        height: 30px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
    }

    .loading span {
        display: inline-block;
        width: 30px;
        height: 100%;
        margin-right: 15px;
        border-radius: 50%;
        background: #56beff;
        -webkit-animation: load 1.04s ease infinite;
    }

    .loading span:last-child {
        margin-right: 0px;
    }

    @-webkit-keyframes load {
        0% {
            opacity: 1;
            -webkit-transform: scale(1.3);
        }
        100% {
            opacity: 0.2;
            -webkit-transform: scale(.3);
        }
    }

    .loading span:nth-child(1) {
        -webkit-animation-delay: 0.13s;
    }

    .loading span:nth-child(2) {
        -webkit-animation-delay: 0.26s;
    }

    .loading span:nth-child(3) {
        -webkit-animation-delay: 0.39s;
    }

    .loading span:nth-child(4) {
        -webkit-animation-delay: 0.52s;
    }

    .loading span:nth-child(5) {
        -webkit-animation-delay: 0.65s;
    }
</style>
