import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './assets/global/style/style.scss'
import 'animate.css'
import 'amfe-flexible/index.js'
import 'es6-promise/auto'
import fnc from './fnc';
import store from './store';
import animate from 'animate.css'
import {Toast} from 'vant';
import MetaInfo from 'vue-meta-info'
import {sernsorFn} from "@/utils/sensors";

Vue.use(MetaInfo)
Vue.use(Toast);
Vue.use(animate)
Vue.config.productionTip = false
Vue.use(fnc);
// eslint-disable-next-line no-unused-vars
window.vm = new Vue({
    router,
    render: h => h(App),
    store,
    mounted() {
        document.dispatchEvent(new Event('render-event'))
    }
}).$mount('#app')

window.addEventListener('load', () => {
    //这里用setTimeout是为了获取到正确的loadEventEnd时间戳
    setTimeout(() => {
        const performance = window.performance.timing;
        const data = {
            r_onload: (performance.loadEventEnd - performance.fetchStart) / 1000,//onload时间
            r_request: (performance.responseEnd - performance.responseStart) / 1000,//请求耗时
            r_white: (performance.domLoading - performance.fetchStart) / 1000,//白屏时间
        };
        sernsorFn('referralPageLoadingTime', data);
        console.log('页面load数据:', data);
    }, 3000);
})

