export function sernsorFn(eventName, data = {}) {
    try {
        console.log(data);
        // eslint-disable-next-line no-undef
        sensors.track(eventName, data);
    } catch (e) {
        console.log(e);
    }
}

export function sernsorlogin(eventName, data = '') {
    try {
        // eslint-disable-next-line no-undef
        sensors.login(data);
    } catch (e) {
        console.log(e);
    }
}
