import request from "../utils/request";

/**
 * 获取页面参数
 * @param bundleId
 * @returns {AxiosPromise}
 */
export function getPageParameter(bundleId, disableLoading = false) {
    return request({
        url: `/s/uis/referral/pageParameter?tag=referralV2.0&bundleId=${bundleId}`,
        method: 'get',
        disableLoading: !!disableLoading,//不显示动画
    });
}


/**
 * 获取分享二维码
 * @param userId
 * @returns {AxiosPromise}
 */
export function getQrCode(userId) {
    return request({
        url: `/s/uis/referral/qrCode?bundleId=com.drpanda.chineseacademy&tag=referralV2.0&userId=${userId}`,
        method: 'get',
        disableLoading: true,//不显示loading 动画
    });
}


/**
 * 领取大礼包
 */
export function getReward(params) {
    return request({
        url: '/s/uis/referral/collectionReward',
        method: 'post',
        data: params,
    });
}


/**
 * 判断用户是付费用户 ture 为付费用户
 * @param bundleId
 * @returns {AxiosPromise}
 */
export function checkUserType(bundleId) {
    return request({
        url: '/s/uis/referral/checkIsPayUser?bundleId=' + bundleId,
        method: 'GET',
    })

}


/**
 * 获取成功邀请的好友列表
 * @param boundleId
 * @returns {AxiosPromise}
 */
export function getSuccessFriends(boundleId) {
    return request({
        url: `/s/uis/referral/successRefUsers?bundleId=${boundleId}&tag=referralV2.0`
    })

}


/**
 * 获取活动页面的 二维码
 * @param boundleId
 * @param disableLoading
 * @returns {*}
 */
export function getPageUrlQrCode(boundleId, disableLoading = false) {

    return request({
        url: `/s/uis/referral/appQrCode?bundleId=${boundleId}`,
        method: 'get',
        disableLoading: !!disableLoading,//不显示动画
    })

}
