// 此处用于声明公共的方法 ，此处的方法可以全局调用

exports.install = function (Vue) {


    /**
     * 跳转路由
     * @param name
     * @param query
     */
    Vue.prototype.dp_go = function (name, query) {
        this.$router.push({name, query})
    };


    /**
     * 返回上一页
     */
    Vue.prototype.dp_goBack = function () {
        this.$router.go(-1);//返回上一层
    };


    /**
     * 返回上一页
     */
    Vue.prototype.dp_goBack = function () {
        this.$router.go(-1);//返回上一层
    };

    /**
     *通用的全局提示
     * @param title
     * @param icon
     * @param content
     */
    Vue.prototype.dp_Tips = function (title = '', content = '', icon = '',showClose=true,) {
        this.$store.dispatch('showGlobalTips', {title, icon, content,showClose})
    }

    /**
     * 关闭 通用的全局提示
     */
    Vue.prototype.dp_CloseTips = function () {
        this.$store.commit('setGlobalTips', {
            show: false,
            title: "提示",
            icon: '',
            content: "",
        });
    }



    /**
     * 前往错误页
     * @param code
     * @param tips
     * @param showGoBack
     * @param jumpUrl
     */
    Vue.prototype.dp_goError = function (code = '', tips = '出错了', showGoBack = true, jumpUrl = '') {
        const info = encodeURIComponent(JSON.stringify({code, tips, showGoBack, jumpUrl}));
        this.dp_go('error', {info});
    };

    // /**
    //  * 获取url中的参数
    //  * @param name
    //  * @returns {string|null}
    //  */
    // Vue.prototype.dp_getQueryString = function (name) {
    //     if ("URLSearchParams" in window) {
    //         const searchParams = new URLSearchParams(window.location.search);
    //         return searchParams.get(name) || null;
    //     }
    //     const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    //     const r = window.location.search.substr(1).match(reg);
    //     if (r != null) {
    //         return decodeURIComponent(r[2]);
    //     }
    //     return null;
    // }

    /**
     * 展示全屏提示
     * @param title
     * @param content
     */
    Vue.prototype.dp_showFullScreenTips = function (title, content) {
        this.$store.commit('setFullScreenTips', {
            display: true, title, content
        })
    };

    /**
     * 关闭全屏提示
     */
    Vue.prototype.dp_closeFullScreenTips = function () {
        this.$store.commit('setFullScreenTips', {display: false, title: '', content: ''})
    };

    /**
     * 节流函数
     * @param fn
     * @param wait
     * @param immediate
     * @returns {Function}
     */
    Vue.prototype.dp_debounce = function (fn, wait, immediate) {
        immediate = immediate || false;
        var timer = null;
        var count = 0;
        return function () {
            var _this = this;
            var _arg = arguments;
            clearTimeout(timer);
            if (immediate && !count) {
                fn.apply(_this, _arg);
                count++;
            } else {
                timer = setTimeout(function () {
                    console.log(this);
                    fn.apply(_this, _arg);
                    count++;
                }, wait);
            }
        }

    }

    /**
     * 节流函数
     * @param time
     * @param action
     * @returns {Function}
     */
    Vue.prototype.dp_throttle = function (time, action) {
        var timer = null;
        return function () {
            var context = this;
            var args = arguments;
            if (!timer) {
                timer = setTimeout(function () {
                    action.apply(context, args);
                    timer = null;
                }, time);
            }
        }
    }


};
