<template>
    <div v-if="display" class="DP_Popup">
        <div :style="{backgroundColor}" class="panel">
            <div v-if="icon==='success'" class="icon success "></div>
            <div v-if="icon==='error'" class="icon error "></div>
            <div v-if="title" class="title">
                <b>{{title}}</b>
            </div>
            <div class="content" >
                <slot></slot>
            </div>
            <div class="button-group ">
                <slot name="button"></slot>
            </div>
            <button v-if="showClose" class="close" @click="close"></button>
        </div>

    </div>

</template>

<script>

    export default {
        name: "Popup",
        props: {
            display:{
                default: false,
            },
            showClose: {
                default: true,
            },
            title:{
                default: "",
                type:String,
            },
            backgroundColor: {
                default: "white"
            },
            icon:{
                type:String,
            }
        },
        data() {
            return {
                name: '234',
            }
        },
        methods: {
            close() {
                this.$emit("update:display",false);
            }
        },
        created() {

        }
    }
</script>

<style scoped lang="scss">
    .DP_Popup {
        background-color: rgba(0, 0, 0, 0.5);
        position: fixed;
        width: 100%;
        min-height: 100%;
        z-index: 999;
        top: 0;
        left: 0;
        animation: fadeIn .3s linear;

        .panel {
            animation: zoomIn .3s ease-in-out;
            font-size: 42px;
            width: 780px;
            border-radius: 30px;
            top: 20%;
            left: 50%;
            margin-left: -390px;
            position: absolute;
            padding: 60px 30px;
            box-sizing: border-box;

            .icon{

                margin: 0 auto;
                width: 200px;
                height: 200px;
                margin-bottom: 40px;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 80% 80%;
                animation: flipInY 1.5s ease-in-out;
                animation-fill-mode:forwards;



            }
            .success{
                background-image: url("./assets/success.png");
            }

            .error{
                background-image: url("./assets/error.png");
            }


            .title {
                font-size: 48px;
                font-weight: normal;
                font-stretch: normal;
                line-height: 67px;
                letter-spacing: 0px;
                color: #2b2b2b;
                text-align: center;
                margin: 40px 0;
                margin-top: 0;
            }

            .content{
                text-align: center;
                font-size: 39px;
                color: #6e6e6e;
                padding: 0 30px;
                box-sizing: border-box;
                line-height: 60px;

            }

            .button-group{

                text-align: center;
                animation: fadeIn .5s linear;
                animation-fill-mode:forwards;
                margin-top: 50px;
            }

            .close {
                width: 120px;
                height: 120px;
                position: absolute;
                background-color: transparent;
                background-image: url("../../assets/global/close.png");
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 80% 80%;
                border: none;
                left: 50%;
                margin-left: -60px;
                bottom: -180px;
                z-index: 1;
                animation: rotateIn .8s ease-in-out;
                animation-fill-mode:forwards;
            }
        }
    }

</style>
