import Cookies from 'js-cookie';

export function getQueryString(name) {
    if ("URLSearchParams" in window) {
        const searchParams = new URLSearchParams(window.location.search);
        return searchParams.get(name) || null;
    }
    const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    const r = window.location.search.substr(1).match(reg);
    if (r != null) {
        return decodeURIComponent(r[2]);
    }
    return null;
}


export function wechat_login() {
    const appid = 'wxaa40b508dcc89359'; // appid
    let link = window.location.href;
    link = link.charAt(link.length - 1) !== '/' ? link + '/' : link;
    const redirect_uri = encodeURIComponent(link); // 验证后跳转的网页
    const response_type = 'code'; //
    const scope = 'snsapi_userinfo'; // 请求的权限类型
    const state = 'STATE'; // 跳转的参数
    const url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirect_uri}&response_type=${response_type}&scope=${scope}&state=${state}#wechat_redirect`;
    console.log(url);
    window.location.href = url;
}


export function setCookie(name, value, Days = 180) {
    let days = -1;
    if (!Days) {
        days = 180;
    } else {
        days = Days
    }
    const exp = new Date();
    exp.setTime(exp.getTime() + days * 24 * 60 * 60 * 1000);
    if ((typeof value === "string") && (value.length > 0)) {
        document.cookie = name + "=" + escape(value) + ";expires=" + exp.toUTCString();
    } else {
        const exp = new Date();
        exp.setTime(exp.getTime() - 1);
        const cval = getCookie(name);
        if (cval != null)
            document.cookie = name + "=" + cval + ";expires=" + exp.toUTCString();
    }
}

export function getCookie(name) {
    const arr = document.cookie.match(new RegExp("(^| )" + name + "=([^;]*)(;|$)"));
    if (arr != null) {
        return unescape(arr[2]);
    }
    return null;
}

export function setLocalData(key, value, type = 'session') {
    switch (type) {
        case "local":
            localStorage.setItem(key, utf8_to_b64(value));
            break;
        case "session":
            sessionStorage.setItem(key, utf8_to_b64(value));
            break;
        case "cookies":
            Cookies.set(key, utf8_to_b64(value));
            break;
        default:
            console.warn('本地存储类型错误')
    }
}


export function getLocalData(key, type = 'session') {
    switch (type) {
        case "local":
            return localStorage.getItem(key) ? b64_to_utf8(localStorage.getItem(key)) : null;
        case "session":
            return sessionStorage.getItem(key) ? b64_to_utf8(sessionStorage.getItem(key)) : null;
        case "cookies":
            return Cookies.get(key) ? b64_to_utf8(Cookies.get(key)) : null;
        default:
            console.warn('本地存储类型错误')
    }

}

export function removeLocalData(key, type = 'session') {
    switch (type) {
        case "local":
            return localStorage.removeItem(key);
        case "session":
            return sessionStorage.removeItem(key);
        case "cookies":
            return Cookies.remove(key);
        default:
            console.warn('本地存储类型错误')
    }

}

/**
 * 文本转 b64  支持中文
 *
 * @param str
 * @returns {string}
 */
export function utf8_to_b64(str) {
    return window.btoa(encodeURIComponent(str));
    // return window.btoa(escape(encodeURIComponent(str)));
}

/**
 * b64转中文
 * @param str
 * @returns {string}
 */
export function b64_to_utf8(str) {
    // return decodeURIComponent(unescape(window.atob(str)));
    return decodeURIComponent(window.atob(str));
}

/**
 * 检查手机号格式是否正确  正确  true  错误 false
 * @param phone
 * @returns {boolean}
 */
export function checkPhone(phone) {
    return phone.toString().length === 11 ? /^1[3456789]\d{9}$/.test(phone) : false;
}

export function getEnv() {
    const url = window.location.href;
    let str = 'dev';
    if (url.indexOf('-dev') > -1) {
        str = 'dev';
    } else if (url.indexOf('-pre') > -1) {
        str = 'pre';
    } else if (url.indexOf('localhost') > -1) {
        str = 'dev';
    } else {
        str = 'prod';
    }
    return str;
}


