import request from "@/utils/request";

/**
 * 获取钱包信息
 * @returns {AxiosPromise}
 */
export function getWalletInfo() {
    return request({
        url: `/s/uis/referral/wallet/info`,
        method: 'get'
    });
}


/**
 * 提现
 * @param amount
 * @param name
 */
export function getMoney(amount,name) {
    return request({
        url: `/s/uis/referral/wallet/withdraw?amount=${amount}&name=${name}`,
        method: 'post'
    });
}
