import Vuex from 'vuex'
import Vue from "vue";
import numeral from "numeral";
import {getWalletInfo} from "@/api/wallet";
import {getUserInfoByToken} from "@/api/login";
import {getPageParameter} from "@/api/parameter";

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        env: '',//测试dev  预发布 pre    生产环境 prod
        requestCompleted: false,//异步请求是否完成  用于控制全局loading
        showRegister: false,// 显示注册
        browser: {
            device: {
                isAndroid: false,//安卓
                isIos: false,//ios
                isIphone: false,// iphone
                isIPad: false,//ipad
                isWechat: false,//微信
                isDPApp: false,//在熊猫博士系列app 中 的webview
            },
            clientHeight: 0,//显示区域高度
            isHorizontallyScreen: false,//是否是横屏
        },//浏览器相关特性
        userInfo: {
            data: {
                avatar: '',
                gzhSubscribed: false,
                id: '',
                mobile: '',
                name: '',
                openId: '',
                unionId: '',
            },//用户基础信息
            wallet: {
                totalAsset: '',//钱包总余额 分
                asset: "",//可支配金额 分
                freezeStatus: 0,//冻结状态(0正常，1冻结)
                cumulativeReward: 0, //累计奖金收益
                cumulativeWithdraw: 0,//累计提现金额
                id: null,//钱包 id
            },//用户钱包
            token: "",
        },
        pageParameter: {
            isInit: true,//是否是初始化状态
            activity: '',//活动是否开启
            newcomerVipDays: '',//新人大礼包送的VIP天数
            newComerVipBackground: '',//新人大礼包背景图
            posterAndShareContent: [],//海报和对应的分享语列表
            rewardUpperLimit: '',//现金奖励最高上限
            vipUpperLimit: '',// vip奖励最高上限
            activityRule: '',//活动规则文案
        },
        globalTips: {
            show: false,
            title: "提示",
            icon: '',
            content: "",
            showClose:true,
        },

    },
    getters: {
        getEnv: (state) => {
            return state.env;
        },
        getRequestCompleted: (state) => {
            return state.requestCompleted;
        },
        getBrowser: (state) => {
            return state.browser;
        },
        getUserInfo: (state) => {
            return state.userInfo.data;
        },
        getWallet: (state) => {
            return state.userInfo.wallet;
        },
        getToken: (state) => {
            return state.userInfo.token;
        },
        getPageParameter: (state => {
            return state.pageParameter;
        }),
        getShowRegister: (state => {
            return state.showRegister;
        }),
        getGlobalTips: (state => {
            return state.globalTips;
        })
    },
    mutations: {
        setEnv: (state, env) => {
            state.env = env;
        },
        setRequestCompleted(state, mark) {
            state.requestCompleted = mark;
        },
        setUserInfo: (state, data) => {
            Object.assign(state.userInfo.data, data);
        },
        setToken: (state, data) => {
            state.userInfo.token = data;
        },
        setWallet: (state, data) => {
            Object.assign(state.userInfo.wallet, data);
        },
        setDevice: (state, data) => {
            Object.assign(state.browser.device, data);
        },
        setBrowser: (state, data) => {
            Object.assign(state.browser, data);
        },
        setPageParameter: (state, data) => {
            state.pageParameter = data;
        },
        setFullScreenTips: (state, data) => {
            Object.assign(state.fullScreenTips, data);
        },
        setshowRegister: (state, data) => {
            state.showRegister = data;
        },
        setGlobalTips: ((state, data) => {
            Object.assign(state.globalTips, data);
        })
    },
    actions: {
        async getUserInfo(context) {
            console.log('查询用户信息')
            const res = await getUserInfoByToken();
            const {data: {code, data}} = res;
            if (code === 0) {
                context.commit("setUserInfo", data);
                return true;
            }
        },
        async getPageParameter(context, payload = {}) {
            //暂时写死ca
            const res = await getPageParameter("com.drpanda.chineseacademy", payload.disableLoading);
            const {data: {code, data}} = res;
            if (code === 0) {
                data.isInit = false;
                context.commit('setPageParameter', data);
                return true
            }

            return false
        },
        async getWallet(context) {
            const res = await getWalletInfo();
            const {data: {code, data}} = res;
            if (code === 0) {
                data.totalAsset = data.totalAsset ? numeral(data.totalAsset).divide(100).value() : 0;
                data.asset = data.asset ? numeral(data.asset).divide(100).value() : 0;
                data.cumulativeWithdraw = data.cumulativeWithdraw ? numeral(data.cumulativeWithdraw).divide(100).value() : 0;
                context.commit("setWallet", data);
            }
        },
        async showGlobalTips(context, {title, icon, content,showClose}) {
            const now = context.getters.getGlobalTips.show;
            if (now) {
                context.commit('setGlobalTips', {
                    show: false,
                    title: "提示",
                    icon: '',
                    content: "",
                });
                setTimeout(() => {
                    context.commit('setGlobalTips', {
                        show: true,
                        title: title || '提示',
                        icon: icon || '',
                        content: content || '',
                        showClose,
                    })
                }, 1000)
            } else {
                context.commit('setGlobalTips', {
                    show: true,
                    title: title || '提示',
                    icon: icon || '',
                    content: content || '',
                    showClose,
                })
            }
        }
    }
})
export default store;
