import Vue from 'vue'
import VueRouter from 'vue-router'
import {getEnv, getLocalData, setLocalData} from "@/utils/function";
// import store from "@/store";
const ossConfig = require('../../oss.config');
//const env = process.env.PROFILE || 'dev';
const env = getEnv();
const directory = ossConfig.directory ? `/${ossConfig.directory[env]}/` : null;


Vue.use(VueRouter)


const routes = [
    {
        path: '/',
        name: 'index',
        component: () => import( '../views/index'),
        meta: {
            title: '首页',
            description: ''
        }
    },
    {
        path: '/poster',
        name: 'poster',
        meta: {
            title: '分享海报',
            requireAuth: true,
        },
        component: () => import( '../views/poster'),
    },
    {
        path: '/poster2',
        name: 'poster2',
        meta: {
            title: '分享海报',
        },
        component: () => import( '../views/poster/poster_2'),
    },
    {
        path: '/invitation-detail',
        name: 'invitation-detail',
        component: () => import( '../views/invitation-detail/'),
        meta: {
            title: '推荐奖励',
            requireAuth: true,
        }
    },
    {
        path: '/account-center',
        name: 'account-center',
        component: () => import( '../views/account-center'),
        meta: {
            title: '账户中心',
            requireAuth: true,
        }
    },
    {
        path: '/cash-back',
        name: 'cash-back',
        component: () => import( '../views/wallet/cash-back'),
        meta: {
            title: '提现',
            requireAuth: true,
        }
    },
    // {
    //     path: '/balance-history',
    //     name: ' balance-history',
    //     component: () => import( '../views/wallet/balance-history'),
    //     meta: {
    //         title: '我的邀请历史',
    //         requireAuth: true,
    //     }
    // },

    {

        path: '/ca',
        name: 'ca',
        component: () => import( '../views/ca/ca-layout'),
        children: [
            // {path: '/', redirect: 'home'},
            {
                path: '/',
                name: 'recommend',
                component: () => import( '../views/ca/recommend'),
                meta: {
                    title: '邀请好友',
                    desc:'熊猫博士识字-荐多识广得好礼',
                    requireAuth: true,
                }
            },
            {
                path: 'invitee',
                name: 'invitee',
                component: () => import( '../views/ca/invitee'),
                meta: {
                    title: '欢迎下载熊猫博士识字',
                    desc:'熊猫博士识字-荐多识广得好礼',
                }
            }
        ]
    },
    {
        path: '/wechat-login',
        name: 'wechat-login',
        component: () => import( '../views/auth/wechat'),
        meta: {
            title: '微信授权'
        }
    },
    // {
    //     path: '*',    // 此处需特别注意至于最底部
    //     redirect: '/chengdu'
    // }
    {
        path: '/error',
        name: 'error',
        component: () => import( '../views/public/error'),

    },
    {
        path: '*',
        name: 'not-found',
        component: () => import( '../views/public/not-found'),
    }
]

const router = new VueRouter({
    mode: 'history',
    base: directory,
    routes
})


// eslint-disable-next-line no-unused-vars
router.beforeEach(async (to, from, next) => {
    const isWechat = /micromessenger/.test(window.navigator.userAgent.toLocaleLowerCase());
    const token = getLocalData('referral-token', 'cookies');
    const store = router.app.$options.store;
    if (token) {
        store.commit('setToken', token);
    }
    const vuex_token = store.getters.getToken;
    console.log('微信环境', isWechat);
    if (isWechat) {
        if (to.meta.requireAuth) {
            if (!vuex_token) {
                setLocalData('dp-redirect', to.fullPath, 'session');
                return next({path: '/wechat-login'});//当前的导航被中断，然后进行一个新的导航
            }
            if (!store.getters.getUserInfo.id) {
                //如果在app中直接放行
                if (store.getters.getBrowser.device.isDPApp) {
                    return next();
                }
                const res = await store.dispatch('getUserInfo');
                if (res) return next();
            } else {
                return next();
            }

        } else {

            if (vuex_token && to.name === 'wechat-login') {
                console.log('已经有token了  不允许进入授权页面');
                return next({path: '/account-center'});//当前的导航被中断，然后进行一个新的导航

            }


            return next();

        }


        console.log('微信jssdk---------------------------------------',to.fullPath);

    } else {
        next();
    }


})

export default router
