import axios from 'axios';
import errorCode from "@/utils/errorCode";

// import {setLocalData} from "./function";
import {globalLoading} from "./gloabLoading.js";
import {getEnv, removeLocalData} from "@/utils/function";
import {sernsorFn} from "@/utils/sensors";

const url = {
    dev: "https://staging-subs.xiongmaoboshi.com",
    pre: "https://subs-pre.xiongmaoboshi.com",
    prod: "https://subs.xiongmaoboshi.com",

}

const request = axios.create({
    baseURL: url[getEnv()],
    timeout: 20000,
    headers: {
        "Content-Type": "application/json"
    }
});
request.interceptors.request.use(function (config) {

    config.metadata = {startTime: new Date(), url: config.url}

    if (config.disableLoading) {
        console.log("无需加载动画", config.url);
    } else {
        globalLoading.showFullScreenLoading();
    }

    let token = window.vm.$store.getters.getToken;//从vuex 中取出 操作员信息
    if (token) {
        //去除token首尾意外出现的引号
        const arr = token.split('');
        if (arr[0] === '"' && arr[arr.length - 1] === '"') {
            arr.splice(0, 1);
            arr.splice(arr.length - 1, 1);
        }
        token = arr.join('');
        config.headers.Authorization = token;
    }


    // 在发送请求之前做些什么
    return config;

}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});

request.interceptors.response.use(function (response) {
    response.config.metadata.endTime = new Date();
    response.duration = {
        r_apiUrl: response.config.metadata.url,
        r_time: (response.config.metadata.endTime - response.config.metadata.startTime) / 1000,
    };
    sernsorFn('referralApiTime', response.duration);
    console.log('api监测', response.duration);
    // eslint-disable-next-line no-unused-vars
    const {data, status, headers} = response;

    const vue = window.vm;
    //globalLoading.tryHideFullScreenLoading();

    // 错误的业务码 弹出提示
    const code = data['code'];
    console.log("业务状态", code);
    switch (code) {
        case 0:
            break;
        case 1080:
        case 1070:
        case 4024:
            removeLocalData('referral-token', 'cookies');
            vue.$store.commit("setToken", '');
            vue.dp_go('wechat-login');
            break;
        default:
            vue.dp_Tips('提示', errorCode[code] || '出错了 请重试');
    }
    return response;
}, function (error) {
    // globalLoading.tryHideFullScreenLoading();

    globalLoading.tryHideFullScreenLoading();
    const {response: {status, data: {code}}} = error;
    if (status >= 500) {
        if (code) {
            const vue = window.vm;
            vue.dp_Tips('提示', errorCode[code] || '系统错误');
        }
        // window.vm.$toast(`服务器错误 状态码：${status}`);
    }

    // if (status === 401) {
    //     vueObj.ele_notify("未登录", "error", status);
    // } else if (status === 404) {
    //
    //     vueObj.ele_notify("资源请求错误", "error", status);
    // } else if (status >= 500) {
    //     vueObj.ele_notify("服务器错误", "error", status);
    // }


    // if (response.data) {
    //   switch (response.data["code"]) {
    //     case 10400:
    //       console.log("请登录");
    //       break;
    //     case 10401:
    //       console.log("权限验证失败操作");
    //       break;
    //     default:
    //       console.log(data.code, data.message);
    //   }
    // }

    return Promise.reject(error);
});


export default request;
