import request from "../utils/request";

export function loginbyWechatCode(code) {
    return request({
        url: `/ns/qk/account/wechat/userinfo?code=${code}`,
        method: 'get'
    });
}

/**
 * UnionId方式登录
 */
export function loginByUnionId(params) {
    return request({
        url: '/ns/qk/account/login-by-unionid',
        method: 'post',
        data: params,
    });
}

/**
 * 获取手机验证码
 */
export function getMobileCode(telphone) {
    return request({
        url: '/code/send',
        method: 'post',
        data: {
            key: '+86' + telphone,
            type: 12,
        },
        disableLoading:true,//不显示loading 动画
    })
}


/**
 * 手机号绑定微信
 * @param params
 * @returns {AxiosPromise<any>}
 */
export function bindPhone(telphone, vertifyCode, avatar, name, unionId) {
    return request({
        url: '/ns/qk/account/login-register',
        method: 'post',
        data: {
            mobile: "+86" + telphone,
            smsCode: vertifyCode,
            avatar: avatar,
            name: name,
            unionId: unionId,
        },
     disableLoading:true,//不显示loading 动画
    })
}


/**
 * 根据token 获取用户信息
 * @returns {AxiosPromise}
 */
export function getUserInfoByToken() {
    return request({
        url: '/s/account/profile',
        method: 'get',
    })

}
